import React, { useEffect, useState } from 'react';
import './App.css';
import './style.css'
import question from './question';
import { Button, Checkbox, Image, Input, Radio, Space } from 'antd';
import Flags from './Flags';



function Authorization(props) {
    const { authorize, setAuthorize, setRoute, route } = props
    const [login, setLogin] = useState('')
    const [password, setPassword] = useState('')
    const user = {login: 'admin', password: 'admin', name: 'Денис'}
    useEffect(() => {
        if (authorize === 'Денис') setRoute('victorina')  
    }, [authorize]);
  return (
    <div>
    <div className="heading">
        <h1 style={{ zIndex: 1000 }}>Введите логин и пароль</h1>
    </div>
    <div style={{ zIndex: 3, display:'grid' }} >
        <div style={{ display: 'flex', justifyContent: 'center' }}>
        <Input onChange={e => setLogin(e.target.value)} style={{ width: 300 }} size='large' aria-label='Login' placeholder='Login'></Input>
        </div>
        <div style={{ height: 40 }}> </div>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
        <Input onChange={e => setPassword(e.target.value)} style={{ width: 300 }} size='large' aria-label='Password' placeholder='Password'></Input> 
        </div>
        <div style={{ height: 40 }}> </div>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
        <Button style={{ width: 200 }} size='large' onClick={() => ( login === user.login && password === user.password ) && setAuthorize(user.name) }> Продолжить </Button> 
        </div>
    </div>
    <div className='next'>
    </div>
    </div>
  );
}

export default Authorization;
