import React, { useEffect, useState } from 'react';
import './style.css'
import Main from './Main';
import Quiz from './Quiz';
import { Button } from 'antd';
import main from './images/Main.jpeg'
import Authorization from './Autorization';


function App() {
  const [route, setRoute] = useState('authorize')
  const [authorize, setAuthorize] = useState('')
  useEffect(() => {
  },[])
  return (
        <div style={route === 'main' ? {   backgroundImage: `url("${main}")` } : {}} class="wrapper">
        <header className='navbar'>
            <nav>
              <Button className='item' type='primary' onClick={() => setRoute('main')}>
          Главная
          </Button>
          <Button className='item' type='primary' onClick={() => setRoute('victorina')}>
          Викторина
          </Button>
          <Button className='item' type='primary' onClick={() => setRoute('authorize')}>
          Авторизация
          </Button>
          <Button className='item' type='primary' onClick={() => {setRoute('main'); setAuthorize('');}}>
          Выход
          </Button>
        </nav>
        </header>
    
        {/* <img className="main" alt="maintheme" src="images/Main.jpeg" /> */}
          {route === 'main' ? <Main setRoute={setRoute} /> : route === 'authorize'
            ? <Authorization setRoute={setRoute} route={route} authorize={authorize} setAuthorize={setAuthorize}/> : <Quiz name={authorize}/>}
        </div>
  );
}

export default App;
