import React, { useEffect, useState } from 'react';
import './App.css';
import './style.css'
import question from './question';
import { Button, Checkbox, Image, Input, Radio, Space } from 'antd';
import Flags from './Flags';



function Quiz(props) {
  const [questionArr, setQuestionArr] = useState(<></>)
  const [i, setI] = useState(0)
  const [value, setValue] = useState(0);
  const [quiz, setQuiz] = useState([])
  const [answers, setAnswers] = useState([])
  const [result, setResult] = useState([0])
  const [userName, setUserName] = useState('')
  const [inputValue, setInputValue] = useState('')
  const onChange = (e) => {
    setValue(e.target.value)
  };

    const { name } = props
    function getRandomInt(max) {
        return Math.floor(Math.random() * max);
      }
    const getQuestions = () => {
      const arr = []
        for(let i = 0; i < 10; i++ ) {
            arr.push(question[getRandomInt(100)])
        }
        const newAnsw = arr.map(i => i.answerCode)
        const newArr = arr.map(i =>
         <div style={{ display: 'flex' }}>
    <Radio.Group style={{ width: '50%' }} size='large' onChange={onChange} buttonStyle='solid'>
      <Space direction='vertical'>
      <Radio.Button value={1}>{i[1]}</Radio.Button>
      <Radio.Button value={2}>{i[2]}</Radio.Button>
      <Radio.Button value={3}>{i[3]}</Radio.Button>
      <Radio.Button value={4}>{i[4]}</Radio.Button>
      </Space>
    </Radio.Group>
          <div style={{

          }}>
        <Image style={{ width: 200 }} src={Flags[i.answer]} />
        </div>
        </div>
        )
        setAnswers(newAnsw)
      setQuestionArr(newArr)
    }
    useEffect(() => {
        if (i === 0) {
          getQuestions()
          setResult([0])
        }
    }, [i])
    useEffect(() => {
      const a = [...quiz, value]
      if (value !== 0 && i < 11) setQuiz(a)
    }, [i])
    useEffect(() => {
      if (quiz.length === 10) {
        const arrAns = []
        for(let a = 0; a < 10; a++) {
          const check = quiz[a] === answers[a]
          arrAns.push(check)
        }
        const result = arrAns.map(i => i ? 1 : 0)
        const newres = result.reduce(
          (accumulator, currentValue) => accumulator + currentValue,
          0
        );
        setResult([newres])
      }
    }, [quiz])
    useEffect(() => {
      if (name) setUserName(name)
    }, [])
  return (
    <div>
    <div className="heading">
        <h1 style={{ zIndex: 1000 }}>Викторина на знание гербов и флагов стран мира</h1>
    </div>
    <div style={{ zIndex: 3 }} className="fields">
      
        <p>Ваше имя {` ${userName}` || ''}</p>
        <div style={{ height: 20 }}/>
        { !userName ? <><Input style={{ width: 300}} type="text" placeholder="Введите Имя" class="name" onChange={e => setInputValue(e.target.value) }/><div style={{ height: 20 }}/><Button onClick={() => setUserName(inputValue)}> Продолжить </Button> </>: <></>}
        <p className="country">Какой стране принадлежит этот герб-флаг?</p>
    {i < 10 ? questionArr[i]: result.map(i => <p key={i} >{userName}, вы набрали {i} баллов из 10 возможных!</p>)}
    
    </div>
    <div className='next'>
        <Button type='primary' onClick={e => { if(i < 10) {
          setI(i+1)
          } else {
            setI(0)
            setQuiz([])
            setValue(0)
          } }}>{i < 10 ? 'Следующий вопрос' : 'Пройти Викторину Заново'}</Button>
    </div>
    </div>
  );
}

export default Quiz;
